'use client';

/**
 * Arduino Serial Communication Service
 * Handles communication with Arduino device over Web Serial API
 */

// Constants for Arduino communication
const BAUD_RATE = 115200;
const START_BYTE = 0xFF;
const END_BYTE = 0xFE;

type ConnectionCallback = (status: boolean) => void;

class ArduinoService {
  private port: SerialPort | null = null;
  private reader: ReadableStreamDefaultReader<Uint8Array> | null = null;
  private writer: WritableStreamDefaultWriter<Uint8Array> | null = null;
  private connectionStatus: boolean = false;
  private connectionCallbacks: ConnectionCallback[] = [];

  /**
   * Register a callback for connection status changes
   */
  onConnectionChange(callback: ConnectionCallback) {
    this.connectionCallbacks.push(callback);
    return () => {
      this.connectionCallbacks = this.connectionCallbacks.filter(cb => cb !== callback);
    };
  }

  private notifyConnectionChange(status: boolean) {
    this.connectionCallbacks.forEach(callback => callback(status));
  }

  /**
   * Connect to Arduino device
   */
  async connect(): Promise<boolean> {
    try {
      if (!navigator.serial) {
        throw new Error('Web Serial API not supported');
      }

      // Request port access from user
      this.port = await navigator.serial.requestPort({
        // Optional filters for specific Arduino devices
        filters: [
          // Add your Arduino's vendor/product IDs if known
          // { usbVendorId: 0x2341, usbProductId: 0x0043 } // Example for Arduino Uno
        ]
      });

      // Open port with specified baud rate
      await this.port.open({ baudRate: BAUD_RATE });

      // Get reader and writer
      if (this.port.readable && this.port.writable) {
        this.reader = this.port.readable.getReader();
        this.writer = this.port.writable.getWriter();
        this.connectionStatus = true;
        this.notifyConnectionChange(true);
        return true;
      }

      throw new Error('Failed to open serial port streams');
    } catch (error) {
      console.error('Arduino connection failed:', error);
      this.connectionStatus = false;
      this.notifyConnectionChange(false);
      return false;
    }
  }

  /**
   * Disconnect from Arduino device
   */
  async disconnect(): Promise<boolean> {
    try {
      if (this.reader) {
        await this.reader.cancel();
        this.reader.releaseLock();
        this.reader = null;
      }

      if (this.writer) {
        await this.writer.close();
        this.writer.releaseLock();
        this.writer = null;
      }

      if (this.port) {
        await this.port.close();
        this.port = null;
      }

      this.connectionStatus = false;
      this.notifyConnectionChange(false);
      return true;
    } catch (error) {
      console.error('Arduino disconnection failed:', error);
      return false;
    }
  }

  /**
   * Send movement commands to Arduino
   * @param x X-axis movement (-32768 to 32767)
   * @param y Y-axis movement (-32768 to 32767)
   */
  async sendMovement(x: number, y: number): Promise<boolean> {
    if (!this.writer || !this.connectionStatus) {
      throw new Error('Arduino not connected');
    }

    try {
      // Clamp values to 16-bit range
      const clampedX = Math.max(-32768, Math.min(32767, x));
      const clampedY = Math.max(-32768, Math.min(32767, y));

      // Create movement command buffer
      const buffer = new Uint8Array([
        START_BYTE,
        (clampedX >> 8) & 0xFF,  // X high byte
        clampedX & 0xFF,         // X low byte
        (clampedY >> 8) & 0xFF,  // Y high byte
        clampedY & 0xFF,         // Y low byte
        END_BYTE
      ]);

      await this.writer.write(buffer);
      return true;
    } catch (error) {
      console.error('Failed to send movement command:', error);
      return false;
    }
  }

  /**
   * Check if Arduino is connected
   */
  isConnected(): boolean {
    return this.connectionStatus;
  }
}

// Export singleton instance
export const arduinoService = new ArduinoService();
