'use client';

import { arduinoService } from './arduino';

class ArduinoManager {
  private static instance: ArduinoManager;
  private isRunning: boolean = false;
  private intervalId: NodeJS.Timeout | null = null;

  private constructor() {
    if (typeof window !== 'undefined') {
      // Keep connection alive even when window is closed
      window.addEventListener('beforeunload', () => {
        this.persistConnection();
      });
    }
  }

  static getInstance(): ArduinoManager {
    if (!ArduinoManager.instance) {
      ArduinoManager.instance = new ArduinoManager();
    }
    return ArduinoManager.instance;
  }

  async startBackgroundOperation() {
    if (this.isRunning) return;

    try {
      await arduinoService.connect();
      this.isRunning = true;

      // Set up periodic check to maintain connection
      this.intervalId = setInterval(() => {
        if (!arduinoService.isConnected()) {
          arduinoService.connect().catch(console.error);
        }
      }, 5000);

      // Store connection state
      localStorage.setItem('arduino-running', 'true');
    } catch (error) {
      console.error('Failed to start background operation:', error);
    }
  }

  async stopBackgroundOperation() {
    if (!this.isRunning) return;

    try {
      await arduinoService.disconnect();
      if (this.intervalId) {
        clearInterval(this.intervalId);
        this.intervalId = null;
      }
      this.isRunning = false;
      localStorage.removeItem('arduino-running');
    } catch (error) {
      console.error('Failed to stop background operation:', error);
    }
  }

  private persistConnection() {
    if (this.isRunning) {
      localStorage.setItem('arduino-running', 'true');
    }
  }

  isActive(): boolean {
    return this.isRunning;
  }

  // Check if the connection should be restored on page load
  async checkAndRestoreConnection() {
    const wasRunning = localStorage.getItem('arduino-running') === 'true';
    if (wasRunning) {
      await this.startBackgroundOperation();
    }
  }
}

export const arduinoManager = ArduinoManager.getInstance();
