'use client';

import { useState } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { ArduinoControl } from '@/components/ArduinoControl';
import { Alert, AlertDescription } from '@/components/ui/alert';

export default function SettingsPage() {
  const [autoStart, setAutoStart] = useState(false);
  const [debugMode, setDebugMode] = useState(false);
  const [configName, setConfigName] = useState('default');
  const [showSaveSuccess, setShowSaveSuccess] = useState(false);

  const handleSaveConfig = () => {
    // Save current configuration
    const config = {
      autoStart,
      debugMode,
      configName,
      // Add other settings as needed
    };
    
    localStorage.setItem('valorant-config', JSON.stringify(config));
    setShowSaveSuccess(true);
    setTimeout(() => setShowSaveSuccess(false), 3000);
  };

  const handleLoadConfig = () => {
    const savedConfig = localStorage.getItem('valorant-config');
    if (savedConfig) {
      const config = JSON.parse(savedConfig);
      setAutoStart(config.autoStart);
      setDebugMode(config.debugMode);
      setConfigName(config.configName);
    }
  };

  const handleResetConfig = () => {
    setAutoStart(false);
    setDebugMode(false);
    setConfigName('default');
    localStorage.removeItem('valorant-config');
  };

  return (
    <div className="container mx-auto p-8">
      <div className="grid gap-8 max-w-4xl mx-auto">
        <ArduinoControl />

        <Card>
          <CardHeader>
            <CardTitle>General Settings</CardTitle>
          </CardHeader>
          <CardContent className="space-y-6">
            {showSaveSuccess && (
              <Alert>
                <AlertDescription>
                  Settings saved successfully!
                </AlertDescription>
              </Alert>
            )}

            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <Label htmlFor="auto-start">Auto Start on Launch</Label>
                <Switch
                  id="auto-start"
                  checked={autoStart}
                  onCheckedChange={setAutoStart}
                />
              </div>

              <div className="flex items-center justify-between">
                <Label htmlFor="debug-mode">Debug Mode</Label>
                <Switch
                  id="debug-mode"
                  checked={debugMode}
                  onCheckedChange={setDebugMode}
                />
              </div>

              <div className="space-y-2">
                <Label>Configuration Name</Label>
                <Input
                  value={configName}
                  onChange={(e) => setConfigName(e.target.value)}
                  placeholder="Enter configuration name"
                />
              </div>

              <div className="flex gap-4 pt-4">
                <Button onClick={handleSaveConfig}>
                  Save Configuration
                </Button>
                <Button onClick={handleLoadConfig} variant="outline">
                  Load Configuration
                </Button>
                <Button onClick={handleResetConfig} variant="destructive">
                  Reset to Default
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>About</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-2 text-sm text-zinc-400">
              <p>Valorant Arduino Control</p>
              <p>Version: 1.0.0</p>
              <p>A web-based GUI for controlling Arduino-based Valorant assistance</p>
              <p className="text-xs">For educational purposes only</p>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
